// css reset
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
a {
    text-decoration: none;
    color: #000;
}
li {
    list-style: none;
}
button {
    border: none;
    background: transparent;
    outline: none;
}
input:focus::-webkit-input-placeholder, textarea:focus::-webkit-input-placeholder {
    color:transparent;
}
input:focus:-moz-placeholder, textarea:focus:-moz-placeholder {
    color:transparent;
} /* FF 4-18 */
input:focus::-moz-placeholder, textarea:focus::-moz-placeholder {
    color:transparent;
} /* FF 19+ */
input:focus:-ms-input-placeholder, textarea:focus:-ms-input-placeholder {
    color:transparent;
} /* IE 10+ */
// fonts
@font-face {
    font-family: 'Lato';
    src: url('../fonts/Lato/Lato-Regular.eot');
    src: local('Lato Regular'), local('Lato-Regular'),
        url('../fonts/Lato/Lato-Regular.eot?#iefix') format('embedded-opentype'),
        url('../fonts/Lato/Lato-Regular.woff2') format('woff2'),
        url('../fonts/Lato/Lato-Regular.woff') format('woff'),
        url('../fonts/Lato/Lato-Regular.ttf') format('truetype'),
        url('../fonts/Lato/Lato-Regular.svg#Lato-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Lato';
    src: url('../fonts/Lato_light/Lato-Light.eot');
    src: local('Lato-Light'),
        url('../fonts/Lato_light/Lato-Light.eot?#iefix') format('embedded-opentype'),
        url('../fonts/Lato_light/Lato-Light.woff2') format('woff2'),
        url('../fonts/Lato_light/Lato-Light.woff') format('woff'),
        url('../fonts/Lato_light/Lato-Light.ttf') format('truetype'),
        url('../fonts/Lato_light/Lato-Light.svg#Lato-Light') format('svg');
    font-weight: 300;
    font-style: normal;
}
// shared code
body {
    font-family: 'Lato', Arial, sans-serif;
}
.container {
    margin: 0 auto;
    width: 1200px;
}
.section-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    padding: 12vh 0;
}
.title {
    text-align: center;
    font-size: 40px;
    letter-spacing: 2px;
    word-spacing: 0.5vw;
    margin-bottom: 5vh;
}
.desc {
    font-weight: 300;
    font-size: 16px;
    line-height: 22px;
}
.btn {
    padding: 1.5vh 3vw;
    border: 1px solid #000;
    font-size: 12px;
    letter-spacing: 0.5px;
    font-weight: 700;
    text-transform: uppercase;
    background-color: #fff;
    color: #000;
    transition: 0.5s;
}
.btn:hover {
    background-color: #000;
    color: #fff;
}
// header
.header__container {
    height: 80px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #fff;
    -webkit-box-shadow: 0px 1px 10px -1px rgba(0,0,0,0.6);
    -moz-box-shadow: 0px 1px 10px -1px rgba(0,0,0,0.6);
    box-shadow: 0px 1px 10px -1px rgba(0,0,0,0.6);
    .container {
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
.nav {
    display: flex;
}
.nav__mobile {
    display: none;
}
.nav__menu {
    display: flex;
}
.nav__item {
    margin-right: 3vw;
    cursor: pointer;
}
// hero
.hero__container {
    width: 100%;
    margin-top: 80px;
    height: calc(100vh - 80px);
    background-color: #f2f3f3;
}
// about
.about__intro {
    width: 40%;
    margin-bottom: 16vh;
}
.about__intro__desc {
    margin-bottom: 4vh;
    text-align: justify;
}
.about__intro__pdf {
    float: right;
    margin-top: 2vh;
}
.about__skills {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.about__skills__title {
    margin-bottom: 12vh;
}
.about__skills__content {
    width: 60%;
    display: grid;
    grid-template-columns: auto auto auto;
    justify-content: space-around;
    grid-row-gap: 8vh;
}
.about__skills__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    .about__skills__item__icon {
        font-size: 100px;
        margin-bottom: 1vh;
        color: #ccc;
    }
}
// projects
.projects__container {
    padding-bottom: 0;
}
.projects__item {
    width: 100%;
    padding: 10vh 0;
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
.projects__item:nth-child(2n+1) {
    background-color: #f2f2f2;
    .container {
        flex-direction: row-reverse;
    }
}
.projects__item__photo {
    width: 35%;
    border: 1px solid #333;
    height: 40vh;
}
.projects__item__intro {
    width: 45%;
}
.projects__item__intro__title {
    font-size: 18px;
    letter-spacing: 0.4px;
    word-spacing: 0.3vw;
    text-transform: uppercase;
    margin-bottom: 3vh;
}
.projects__item__intro__desc {
    margin-bottom: 2vh;
    line-height: 24px;
}
.projects__item__intro__btn {
    float: right;
}
.projects__item__intro__desc:nth-child(3) {
    margin-bottom: 7vh;
}
// contact
.contact__container {
    background-color: #c1c1c2;
}
.contact__details {
    margin-bottom: 5vh;
    text-align: center;
}
.contact__details__desc:nth-child(1) {
    margin-bottom: 3vh;
}
.contact__message {
    width: 30%;
}
.contact__message__form {
    width: 100%;
    display: flex;
    flex-direction: column;
}
.contact__message__form__field {
    margin-bottom: 1vh;
    border: none;
    padding: 1.2vh 1vw;
    outline: none;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.3px;
}
.contact__message__form__field:nth-child(3) {
    height: 130px;
}
input::placeholder, textarea::placeholder {
    opacity: 0.7;
    font-family: inherit;
    letter-spacing: 0.3px;
}
.contact__message__form__btn {
    width: auto;
    align-self: flex-end;
    margin-top: 2vh;
}
// footer
.footer__container {
    width: 100%;
    background-color: #333;
    height: 120px;
    .container {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
}
.footer__social-media {
    margin-bottom: 1vh;
}
